<template>
  <v-container class="pt-7">
    <v-row
      justify="space-around"
      align="center"
    >
      <Logo />
      <div>
        <v-tabs v-model="langIndex">
          <v-tab
            v-for="lang in langs"
            :key="lang.locale"
            @click="changeLocale(lang.locale)"
          >
            {{ lang.name }}
          </v-tab>
        </v-tabs>
      </div>
    </v-row>
    <v-row
      justify="center"
    >
      <div>
        <div
          class="mt-5 text-h5 text-center grey--text text--darken-1"
          :class="{ 'v-hidden': showingDemo }"
        >
          {{$t('home.slogan')}}
        </div>

        <v-row
          justify="center"
          class="mt-5 mb-5"
        >
          <v-btn
            color="primary"
            @click="openDialog"
          >
            {{$t('home.createPerformance')}}
          </v-btn>
        </v-row>
        <v-row
          justify="center"
        >
          <v-btn
            color="primary"
            @click="showDemo"
            outlined
            class="mb-2"
          >
            <v-icon left>mdi-play</v-icon> {{$t('home.demo')}}
          </v-btn>
        </v-row>

        <div
          class="text-center"
          :class="{ 'v-hidden': showingDemo }"
        >
          <small>
            <v-icon size="18">mdi-arrow-down</v-icon>
            {{$t('home.selectColor')}}
          </small>
        </div>

        <div class="mt-2 mb-5">
          <v-row
            v-for="rowIndex in 3"
            justify="center"
            :key="rowIndex"
            class="mb-3"
          >
            <v-icon
              v-for="index in 5"
              :color="getColorStr(phones[`${rowIndex}_${index}`])"
              @click="changeColor(phones[`${rowIndex}_${index}`])"
              size="58"
              :key="index"
            >
              mdi-cellphone-iphone
            </v-icon>
          </v-row>
        </div>

        <div
          class="mb-7 text-center"
          :class="{ 'v-hidden': showingDemo }"
        >
          * {{$t('home.demoDescription')}}
        </div>
        <div class="mb-5">
          <Rectangle
            :width=16
            :height=9
          >
            <iframe
              src="https://player.vimeo.com/video/446534518"
              width="100%"
              height="100%"
              frameborder="0"
              allow="fullscreen"
              allowfullscreen
            />
          </Rectangle>
        </div>
        <v-card class="elevation-3">
          <v-toolbar
              color="primary"
              dark
              flat
          >
            <v-toolbar-title>
              {{$t('home.howItWorks')}}?
            </v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <div
              v-for="stepIndex in 5"
              :key="`step_${stepIndex}`"
              class="step"
            >
              <v-row class="ml-0 mr-0 no-gutters">
                <div>
                  <v-icon size="30" color="primary" left>mdi-checkbox-marked-circle</v-icon>
                </div>
                <v-col xs="9" sm="10" md="11">
                  <div>{{$t(`steps.${stepIndex}.title`)}}</div>
                  <small class="primary--text">
                    {{$t(`steps.${stepIndex}.description`)}}
                  </small>

                  <div class="pa-3">
                    <div v-if="stepIndex === 1">
                      <v-icon
                        size="58"
                        color="primary darken-2"
                      >
                        mdi-cellphone-iphone
                      </v-icon>
                      <span class="text-h6 pr-3">+</span>
                      <v-icon
                        size="35"
                        color="primary darken-2"
                      >
                        mdi-qrcode-scan
                      </v-icon>
                    </div>
                    <div v-if="stepIndex === 2">
                      <v-icon
                        v-for="iconIndex in 3"
                        :key="`iconStep2_${iconIndex}`"
                        size="36"
                        color="primary darken-2"
                      >
                        mdi-account-check
                      </v-icon>
                    </div>
                    <div v-if="stepIndex === 3">
                      <v-icon
                        size="58"
                        color="primary lighten-1"
                      >
                        mdi-cellphone-iphone
                      </v-icon>
                    </div>
                    <div v-if="stepIndex === 4">
                      <v-icon
                        v-for="iconIndex in 3"
                        :key="`iconStep4_${iconIndex}`"
                        size="58"
                        :color="`primary lighten-${iconIndex}`"
                      >
                        mdi-cellphone-iphone
                      </v-icon>
                    </div>
                    <div v-if="stepIndex === 5">
                      <div
                        v-for="rowIndex in 3"
                        :key="`rowStep5_${rowIndex}}`"
                        class="mb-1"
                      >
                        <v-icon
                          v-for="iconIndex in 3"
                          :key="`iconStep5_${rowIndex}_${iconIndex}`"
                          size="35"
                          :color="`primary ${stepPhones[`${rowIndex}_${iconIndex}`]? 'darken-2' : 'lighten-4'}`"
                        >
                          mdi-cellphone-iphone
                        </v-icon>
                      </div>
                    </div>
                  </div>
                </v-col>
              </v-row>
              <v-divider v-if="stepIndex < 5" class="mb-5" />
            </div>
          </v-card-text>
        </v-card>
      </div>
    </v-row>
    <v-row
      justify="space-around"
      align="center"
      class="mt-8"
    >
      <div class="text-center">
        <div>
          <a href="https://t.me/roman_orlovskiy" target="_blank">
            @roman_orlovskiy
          </a>
        </div>
        <div class="primary--text">
          <small>oOpen.space. 2020</small>
        </div>
      </div>
      <div>
        <img v-if="$i18n.locale === 'ru'" width="145" alt="" src="/icons/made-in-russia-sign-ru.svg" />
        <img v-else width="120" alt="" src="/icons/made-in-russia-sign-en.svg" />
      </div>
    </v-row>

    <v-dialog
      v-model="dialog"
      width="500"
    >
      <Login
        :onCancel="closeDialog"
      />
    </v-dialog>
  </v-container>
</template>

<script>
  import colors from 'vuetify/lib/util/colors';
  import Logo from '../components/Logo';
  import Rectangle from '../components/Rectangle';
  import Login from '../components/Login';
  import { cloneDeep } from 'lodash';

  export default {
    name: 'Home',
    components: {
      Logo,
      Rectangle,
      Login
    },
    data() {
      const langs = [
        {name: 'Русский', locale: 'ru'},
        {name: 'English', locale: 'en'},
      ];
      const langIndex = langs.findIndex(({locale}) => {
        return locale === this.$i18n.locale;
      });

      return {
        showingDemo: false,
        langIndex,
        langs,
        phones: {
          '1_1': {color: 'red'},
          '1_2': {color: 'pink'},
          '1_3': {color: 'purple'},
          '1_4': {color: 'deep-purple'},
          '1_5': {color: 'indigo'},
          '2_1': {color: 'blue'},
          '2_2': {color: 'light-blue'},
          '2_3': {color: 'cyan'},
          '2_4': {color: 'teal'},
          '2_5': {color: 'green'},
          '3_1': {color: 'light-green'},
          '3_2': {color: 'lime'},
          '3_3': {color: 'yellow'},
          '3_4': {color: 'amber'},
          '3_5': {color: 'orange'},
        },
        stepPhones: {
          '1_1': true,
          '1_2': true,
          '1_3': true,
          '2_1': true,
          '2_3': true,
          '3_1': true,
          '3_2': true,
          '3_3': true,
        },
        dialog: false
      }
    },
    methods: {
      getColorStr({color, colorMod = 'base'}) {
        return `${color} ${colorMod}`
      },
      changeColor({color, colorMod = 'base'}) {
        const splitColor = color.split('-');
        if (splitColor[1]) {
          splitColor[1] = splitColor[1].charAt(0).toUpperCase() + splitColor[1].slice(1)
        }
        const formattedColor = splitColor.join('');
        const formattedColorMod = colorMod.replace('-', '');

        this.$vuetify.theme.themes.light.primary = colors[formattedColor][formattedColorMod];
        this.$vuetify.theme.themes.light.primaryColor = {
          color: color,
          colorMod: colorMod
        }
      },
      showDemo() {
        this.showingDemo = true;
        const initPhones = cloneDeep(this.phones);
        const primaryColor = this.$vuetify.theme.themes.light.primaryColor
        const backColorObj = {color: primaryColor.color, colorMod: 'lighten-4'};
        const frontColorObj = {color: primaryColor.color, colorMod: 'darken-2'};

        this.setDemoBackground(backColorObj);
        let i = 0;
        const previewColors = [
          {items: ['2_3']},
          {items: ['1_2']},
          {items: ['1_5']},
          {items: ['3_2']},
          {items: ['3_5']},
          {items: ['1_2', '1_4', '2_2', '2_3', '2_4', '3_2', '3_4']},
          {items: []},
          {items: ['1_2', '1_3', '1_4', '2_2', '2_3', '3_2', '3_3', '3_4']},
          {items: []},
          {items: ['1_2', '2_2', '3_2', '3_3', '3_4']},
          {items: []},
          {items: ['1_2', '2_2', '3_2', '3_3', '3_4']},
          {items: []},
          {items: ['1_2', '1_3', '1_4', '2_2', '2_4', '3_2', '3_3', '3_4']},
        ];

        const interval = setInterval(() => {
          if (i >= previewColors.length) {
            clearInterval(interval);
            this.phones = initPhones;
            this.showingDemo = false;

            return;
          }

          const { items } = previewColors[i];
          for (const phoneKey in this.phones) {
            let colorObj;

            if (items.includes(phoneKey)) {
              colorObj = (items.length === 1)? initPhones[phoneKey] : frontColorObj
            } else {
              colorObj = backColorObj;
            }

            this.phones[phoneKey] = colorObj;
          }
          i++;
        }, 500);
      },
      setDemoBackground(colorObj) {
        for (const phoneKey in this.phones) {
          const phone = this.phones[phoneKey];
          phone.color = colorObj.color;
          phone.colorMod = colorObj.colorMod;
        }
      },
      changeLocale(locale) {
        this.$i18n.locale = locale;
      },
      openDialog() {
        this.dialog = true;
      },
      closeDialog() {
        this.dialog = false;
      }
    }
  }
</script>

<style scoped>
  .step {
    font-size: 1rem;
    line-height: 1;
    color: #000;
  }
</style>
